export default {"name":"印度茶的“黑历史”","menus":[{"content":[{"src":"\\abroads\\india\\hls1.jpeg"},{"text":"  有一样东西，在印度街头随处可见。而且世界经济全球化的推动，也要很大程度上归功于它。"},{"text":"它引发了战争，刺激了毒品交易和奴隶贩卖的增长。"},{"text":"而且它在每个国家都广受欢迎。其中，印度是它的最大消费国。"},{"text":"也许你已经猜到了。"},{"text":"没错，它就是“茶”。"},{"src":"\\abroads\\india\\hls2.gif"},{"text":"18世纪时，只有中国种植茶叶。"},{"text":"之后有英国向中国采购大量的茶叶，甚至比丝绸和陶瓷这些具有中国特色的产品畅销得多。但当时面临这样一个问题：即便英国是欧洲茶叶贸易的中心，但中国人就是对他们的东西不敢兴趣，不会去购买。"},{"text":"这样的不对等贸易便引发了经济灾难。"},{"html":"<b>大名鼎鼎的波士顿茶叶倾倒事件</b>"},{"text":"当时的北美人民长期受到殖民者的压迫，他们的内心充满了愤慨，再加上英国对茶叶的垄断就直接导致了他们的生活水平越来越低，这对于最看重人类自身的权利的北美人民来说简直就是极大的侮辱。"},{"html":"所以波士顿倾茶事件爆发的主要原因之一，就是<b>英国人的垄断茶叶的行为已经打乱了价值规律的正常发展。</b>"},{"src":"\\abroads\\india\\hls3.jpeg"},{"html":"由于运输茶叶的商船是属于东印度公司，大多数人认为这些茶叶来源于印度，其实这些茶叶都来自于<b>中国</b>。"},{"html":"波士顿倾茶事件在茶船上有三种红茶和两种绿茶，大部分的茶叶是福建省武夷山市的<b>武夷红茶</b>。"},{"src":"\\abroads\\india\\hls4.jpeg"},{"text":"直到后来的鸦片战争。英国强迫中国打开通商口岸，以便英国能在中国进行一切贸易活动——包括贩卖鸦片，还让中国割让了香港。（直到1997年香港才回归中国）"},{"text":"与此同时，东印度公司的上层领导们已经在筹划着如何避免未来茶叶市场分崩离析了。"},{"html":"<b>这是你不知道的阿萨姆</b>"},{"text":"阿萨姆邦是印度最大的茶叶种植地区，也是世界上的一些地区。这里是印度本土野生茶树品种Camilliasinensis assamica的所在地。"},{"html":"19世纪30年代，<b>第一批茶园建立在了印度的阿萨姆</b>（Assam），当时种植的是从中国带来的茶树。"},{"src":"\\abroads\\india\\hls5.jpeg"},{"text":"早在茶叶成为商品之前，也就是公元1830年左右，印度茶树大都是野生的，分布在印度阿萨姆的东北方的丛林之中。那时，东印度公司一直在尝试种植茶叶。"},{"text":"1824年，阿萨姆并入印度版图，但英国人不知道该拿这块荒凉的地方干什么好。"},{"src":"\\abroads\\india\\hls6.gif"},{"text":"后来，一对植物学家兄弟在阿萨姆山脉上证实了印度本土茶树的发现。他们了解到，当地部落几个世纪以来一直使用茶叶作为食品和饮料。"},{"text":"到19世纪30年代，这些植物的繁殖和培育终于有了历史性的突破，最终成为英国主导的印度茶产业。"},{"text":"阿萨姆地区主要以生产红茶而闻名，这里生产了一些绿茶和白茶，但它们并不为人所知。阿萨姆红茶具有爽口，大胆的风味，可以自己饮用，但也可以很好地融入牛奶和糖。"},{"src":"\\abroads\\india\\hls7.jpeg"},{"text":"由于其浓郁而浓郁的风味，阿萨姆经常被用于早餐茶混合物，这些混合物受到了英国和世界各地其他文化的欢迎。"},{"src":"\\abroads\\india\\hls8.jpeg"},{"text":"印度的红茶举世闻名，像大吉岭红茶、阿萨姆红茶、尼尔吉里红茶等等。印度人的生活中不能没有红茶。丈夫在外面工作时要喝茶，家庭主妇们打发时间的方法也是喝茶。"},{"text":"我们会发现，越来越多的人喜欢喝茶，不只是我们国家，许多国家都如此。茶里面所含的那些物质对我们的身体有着重要的作用，还能预防一些疾病。"},{"text":"喝茶有的时候了解的多了，品一品过后，你会发现味道很不一样。"}]}]}